.App {
    text-align: center;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

body {
    margin: 0;
    padding: 0;
}


/* Menu  */

#navbar {
    position: fixed !important;
    top: 0;
    width: 100%;
    display: flex;
    /* padding: 0 100px; desplaza horizontalmente los elementos*/
    justify-content: space-between;
    align-items: center;
    transition: 0.5s;
    z-index: 1;
    /*mantiene el navBar siempre encima del resto*/
}

.Bloque {
    display: flex;
    border-color: white;
    border-radius: 2px;
    min-height: 100vh;
    min-width: 100vh;
}

.ContainerAmeba {
    display: flex;
    flex-direction: row;
    min-height: 100vh;
    width: 80%;
    margin: auto;
    align-items: center
}

.left-side {
    width: 60%;
}

.right-side {
    width: 40%;
}

.titulo>h1 {
    font-size: 120px;
    font-family: 'Montserrat';
    font-weight: 900;
}

a.active {
    border-bottom: 1px solid #333;
}

div>.nav-item {
    margin: auto;
    padding: 0px 10px 0px 10px;
}

#locals {
    padding-top: 56px;
    position: relative;
}

.overlay {
    position: absolute;
    bottom: 0;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.5);
    /* Black see-through */
    color: #f1f1f1;
    width: 100%;
    height: 100%;
    transition: .5s ease;
    opacity: 0;
    color: white;
    /* padding: 20px; */
    text-align: center;
    font-size: 120px;
    font-family: 'Montserrat';
    font-weight: 900;
    padding: 20% 0;
}


/* CenteredImageTopTitle Component */

.titleCoverCanvas {
    height: 600px;
    width: 1200px;
    background-image: url("./images/PARKFEST.jpg");
    background-position: center;
    background-size: cover;
    opacity: 0.8;
}

.titleCoverImage {
    position: relative;
    bottom: 0;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.5);
    background-size: contain;
    color: #f1f1f1;
    width: 100%;
    height: 100%;
    opacity: 1.2;
    color: white;
    text-align: center;
    font-size: 100px;
    font-family: 'Montserrat';
    font-weight: 900;
    padding: 15% 0;
}

.subtitleCoverImage {
    font-size: 40px;
    font-family: 'Montserrat';
    font-weight: 600;
    /* padding: 20% 0; */
    color: white;
}


/* When you mouse over the container, fade in the overlay title */

#locals:hover .overlay {
    opacity: 1;
}

#festi {
    position: relative;
    /* padding-top: 56px; */
    align-items: center;
    justify-content: center;
}

#festi:hover .overlay {
    opacity: 1;
}

#colabo {
    /* padding-top: 56px; */
    position: flex;
    /* background-color: white; */
    /*solucion para evitar ser pisado por el menu, OJO*/
    /* background-color: rgb(135, 235, 135); */
}

.BGWrapper {
    position: relative;
    margin: auto;
}

.CenteredImage {
    position: relative;
    height: 20%;
    width: auto;
}


/* Animacion de hover title */

@keyframes TitleHovered {
    0% {
        background-color: rgba(255, 0, 0, 0);
    }
    2% {
        background-color: rgba(255, 0, 0, 0.8);
    }
    23% {
        background-color: rgba(255, 255, 0, 0);
    }
    25% {
        background-color: rgba(255, 255, 0, 0.8);
    }
    48% {
        background-color: rgba(0, 0, 255, 0);
    }
    50% {
        background-color: rgba(0, 0, 255, 0.8);
    }
    98% {
        background-color: rgba(0, 128, 0, 0);
    }
    100% {
        background-color: rgba(0, 128, 0, 0.8);
    }
}

div.PowerTitle:hover {
    color: tomato;
    background-color: yellowgreen;
    animation-name: TitleHovered;
    animation-duration: 1s;
    animation-iteration-count: infinite;
}


